// 
import { request } from "@/utils"
// 1. 

export function loginAPI (formData) {
  return request({
    url: '/authorizations',
    method: 'POST',
    data: formData
  })
}

// 2. 

export function getProfileAPI () {
  return request({
    url: '/user/profile',
    method: 'GET'
  })
}